.home-hero{
  font-size : 100%;
  background-image: url("https://images.pexels.com/photos/325185/pexels-photo-325185.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2");
  background-size: cover;
  background-repeat: no-repeat;
  height : 100vh;
  margin: 0;

  .heroWrapper{
    display: flex;
    flex-flow: column wrap;
    margin: 0;
    position: absolute;
    top: 40%;
    left : 19%;

   .main{
     text-align : center;
     font-size : 4rem;
     font-weight: bold;

   }
    .roles{
      font-weight: bold;
      font-size : 1.7rem;
      text-align: center;

    }
    .btn{
      background-color: white;
      font-weight: bold;
      font-size: 1.5rem;
      padding : 10px;
      margin: 5% auto auto;
      border-radius : 12px;
      cursor : pointer;
      font-family : Black Han Sans,Nunito,sans-serif;

      &:hover{
        background-color : black;
        color : white;
      }

    }


  }
}