.projects{
  display: flex;
  flex-direction: column;
  margin-bottom: 4%;
  overflow-x : visible;



  .project-head{
    text-align: center;
    font-weight: bold;

  }

  .project-cards{
    display: flex;
    flex-wrap: nowrap;
    overflow-x: auto;



  }

  .project-cards > * {
    width: 40%;
    margin-right: 10px;
  }
}