.table{
  .tWrapper{
    .text{
      text-align: center;
      font-weight: bold;
      margin : 2%;

    }

    .table{
      margin : auto;
    }
  }
}

.sect{
  display : flex;
  align-items : center;
  align-content: center;


  .atable{
    background-image : url("https://images.pexels.com/photos/325185/pexels-photo-325185.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 20px;
    display: inline-block;
    margin: auto auto 2%;

    .table-heading{
      font-weight: bold;
    }

    .edit{
      background-color: blue;
    }

    .delete{
      background-color : red;
    }

    .delete , .edit , .add{
      color: white;
      border-radius: 20px;
      display: inline-block;
      font-weight: bold;
      margin-right : 20px;
      cursor: pointer;
      padding : 15px;
    }

    .cell{
      display: inline-block;
    }

    .add{
      background-color : black;
      margin-top: 2%;
      margin-left : 2%;
      margin-bottom: 2%;

    }


  }
}

