.project-descr{
  border-radius: 10px;

  min-width: 200px;
  height : 320px;
  padding: 10px;
  border: 2px solid black;
  width: 40%;
  flex-shrink: 0;
  margin: 2% 20px 2% 2%;

  .pd-picture{
    position: relative;
    ;


    .pd-img{
      object-fit: cover;
      width: 100%;
      height: 200px


    }

    .pd-pic-text{
      font-weight: bold;
      position: absolute;
      top:35%;
      margin-left: 39%;
      background-color: rgba(255, 255, 255, 0.52);
      padding : 10px;
      border-radius :5px;
    }


  }

  .pd-text{

    .pd-text-text{
      //width: 70%;
      text-align: center;
      padding: 5px;

    }

    .pd-link{
      text-decoration: underline;
      cursor: pointer;
      font-weight: bold;
      text-align: center;
    }

  }

}
