.navbar{


  .navWrapper{

    .items{
      list-style: none;
      display : flex;
      position : absolute;
      left : 40%;
      width : 40%;

      .item, .hov{
        font-weight: bold;
        font-size: 1.2rem;
        margin-left : 5%;

        &:hover{
          color :white;
          cursor: pointer;
        }

      }
    }
  }
}