.add-form{
  position: absolute;
  margin-left: 40%;

  .add-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 15px;
    padding : 20px;
    background-image: url("https://images.pexels.com/photos/325185/pexels-photo-325185.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2");
    background-size: cover;
    background-repeat: no-repeat;
    height: 40%;
    width: 70%;

    .sect{
      display: flex;
      flex-direction: column;
      padding: 15px;

      .label{
        font-weight: bold;
        //padding-top: 5px;

      }

      .input , .input-link{
        border-radius: 5px;
        width: 100%;
        height : 35px;
        border: none;
        outline: none;

      }
    }

    textarea{
      border-radius: 10px;
      width:100%;
      height : 50px;
      border: none;
    }

    .sects{
      display: flex;
      flex-direction: column;
    }

    .sect-link{
      display: flex;
      flex-direction: row;
      padding: 10px;
      align-items: center;

      .label-link{
        font-weight: bold;


      }

      .input-link{
        flex :1;
        border-radius: 5px;
        width: 100%;
        height : 35px;
        border: none;
        outline: none;

      }


    }

    .button{
      display: flex;
      cursor: pointer;
      background-color: navy;
      border-radius: 15px;
      padding: 10px;
      text-align: center;
      border : none;
      color:white;
      width: 30%;
      margin: 2% 2% 2% 30%;
      word-break: break-word;
      flex: auto;

    }

    .submit{
      font-weight: bold;
      background-color: #48ACF0;

    }

  }
}

