.card{
  display: inline-block;
  margin: 2%;

  .cardWrapper{

    .img-container{
    position : relative;
      &:hover{
        cursor:pointer;
      }

      .img{
        width : 250px;
        height : 270px;
        border-radius : 20px;
        object-fit: cover;


      }

      .img-text{
        background-color: rgba(255, 255, 255, 0.5);
        padding: 15px;
        border-radius: 15px;
        font-weight:bold;
        font-size : 1.8rem;
        position: absolute;
        top : 30%;
        left:10%;
        text-align: center;
        margin: auto;

        &:hover{
          background-color: rgba(0, 0, 0, 0.58);
          color: white;
        }
      }

    }
  }

}