  background-image:url("https://images.pexels.com/photos/325185/pexels-photo-325185.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2");
  background-repeat: no-repeat;
  font-size: 100%;

  .desc, .tech-used , .project-files{
    text-align: center;
    font-weight : bold;
    margin-top: 2%;

    .desc-title{
      font-size: 2rem;

    }

    .desc-text{


    }

  }

  .tech-used{
    .tech-title{

    }

    .tech-text{

    }

  }

  .project-files{
    .file-head{

    }

    .files{
      list-style-type: none;

      .file-sect{

      }
    }

  }

  .site-images{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    //flex-flow: row wrap;
    //margin-left: 5%;


    .im-1{
