}

.contact{
  background-image: url("https://images.pexels.com/photos/302769/pexels-photo-302769.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2");
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  color: white;

  .main-body{

    .main-body-text{
      display: flex;
      flex-direction: column;
      justify-content:center;
      align-items: center;

      .head{
        font-size: 4rem;
        color: white;
        margin-top:5%;
        font-weight : 700;
      }

      .more-text{
        width : 70%;
        margin-top : 5%;
        font-size : 1.3rem;
        color: white;
        margin-bottom: 4%;

      }

      .btn{
        background-color : white;
        color: black;
        padding : 15px;
        border-radius: 10px;
        cursor: pointer;
        border : none;
        font-weight: 800;

        &:hover{
          color :white;
          background-color : black;
        }
      }
    }
  }

}